import Axios from "axios";

export default class Produto {
  static getProduto = async (data) => {
    return await Axios.get(`http://${self.location.hostname}:3000/produto`, {
      params: data,
      headers: { accessToken: localStorage.getItem("accessToken") },
    });
  };

  static getProdutos = async () => {
    return await Axios.get(`http://${self.location.hostname}:3000/produtos`, {
      headers: { accessToken: localStorage.getItem("accessToken") },
    });
  };

  static getProdutosList = async () => {
    return await Axios.get(
      `http://${self.location.hostname}:3000/produtolist`,
      {
        headers: { accessToken: localStorage.getItem("accessToken") },
      }
    );
  };
}
