<template>
  <TelaBase title="Pedidos">
    <v-container>
      <div>
        <div>
          <v-table>
            <thead style="background-color: lightgrey">
              <tr>
                <td class="text-left">Descrição</td>
                <td class="text-left">Emissão</td>
                <td class="text-left">Prev. entrega</td>
                <td class="text-left">Status</td>
                <td class="text-left">
                  <v-btn class="ma-2" @click="OpenDialog()"> Novo </v-btn>
                </td>
              </tr>
            </thead>
            <tbody>
              <tr v-for="pedido in pedidos" :key="pedido.id">
                <td class="text-left">{{ pedido.descricao }}</td>
                <td class="text-left">{{ pedido.data_emissao }}</td>
                <td class="text-left">{{ pedido.previsao_entrega }}</td>
                <td class="text-left">{{ pedido.status }}</td>
                <td class="text-left">
                  <v-btn class="ma-2" dark @click="OpenDialog(pedido)">
                    Editar
                  </v-btn>
                </td>
              </tr>
            </tbody>
          </v-table>
        </div>

        <v-row justify="center">
          <v-dialog
            v-model="dialog"
            fullscreen
            hide-overlay
            transition="dialog-bottom-transition"
            scrollable
            persistent
          >
            <v-card tile>
              <v-toolbar flat dark>
                <v-btn icon @click="dialog = !dialog">
                  <v-icon> mdi-close </v-icon>
                </v-btn>
                <v-toolbar-title>Edição</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-toolbar-items>
                  <v-btn text @click="dialog = !dialog"> Excluir </v-btn>
                </v-toolbar-items>
                <v-toolbar-items>
                  <v-btn text @click="dialog = !dialog"> Salvar </v-btn>
                </v-toolbar-items>
              </v-toolbar>

              <div>
                <v-responsive class="mx-auto" max-width="90%">
                  <div>
                    <v-text-field
                      v-model="ped.descricao"
                      label="Descrição"
                      :rules="rules"
                      clearable
                    ></v-text-field>
                  </div>
                  <v-container>
                    <v-row>
                      <v-col cols="12" md="6">
                        <v-autocomplete
                          v-model="newItem.item"
                          label="Itens"
                          :items="lstProdutos"
                        ></v-autocomplete>
                      </v-col>
                      <v-col cols="12" md="3">
                        <v-text-field
                          type="number"
                          v-model="newItem.quantidade"
                          label="Quantidade"
                          :rules="rules"
                        ></v-text-field>
                      </v-col>
                      <v-col cols="12" md="2">
                        <v-btn icon @click="AddItem(newItem)">
                          <v-icon color="blue"> mdi-plus </v-icon>
                        </v-btn>
                      </v-col>
                    </v-row>
                  </v-container>
                  <div>
                    <v-table>
                      <thead style="background-color: lightgrey">
                        <tr>
                          <td class="text-left">Descrição</td>
                          <td class="text-left">Qtde</td>
                          <td class="text-left" style="width: 20%"></td>
                        </tr>
                      </thead>
                      <tbody>
                        <tr v-for="item in ped.itens" :key="item.id">
                          <td class="text-left">{{ item.descricao }}</td>
                          <td class="text-left">
                            {{ parseFloat(item.quantidade).toFixed(2) }}
                          </td>
                          <td class="text-left">
                            <v-btn class="ma-2" @click="delItem(item.id)">
                              Excluir
                            </v-btn>
                          </td>
                        </tr>
                      </tbody>
                    </v-table>
                  </div>
                </v-responsive>
              </div>
            </v-card>
          </v-dialog>
        </v-row>

        <v-snackbar v-model="showErro" multi-line>
          {{ showErroText }}
          <template v-slot:actions>
            <v-btn variant="text" @click="showErro = !showErro"> Fechar </v-btn>
          </template>
        </v-snackbar>
      </div>
    </v-container>
  </TelaBase>
</template>

<script>
import TelaBase from "../components/TelaBase.vue";
import { mapActions, mapGetters } from "vuex";

export default {
  components: {
    TelaBase,
  },
  data: () => ({
    dialog: false,
    showErro: false,
    showErroText: null,
    ped: {
      descricao: null,
      itens: [],
    },
    newItem: {
      quantidade: 1,
      item: null,
    },
    lstProdutos: [],
    rules: [(value) => !!value || "O campo é obrigatório."],
  }),
  watch: {
    produtos(value) {
      value.forEach((value) => this.lstProdutos.push(value.nome));
    },
  },
  computed: {
    ...mapGetters("pedido", ["pedidos"]),
    ...mapGetters("pedItem", ["pedItens"]),
    ...mapGetters("produto", ["produtos"]),
  },
  methods: {
    async OpenDialog(pedido) {
      this.dialog = !this.dialog;
      if (pedido) {
        this.ped = pedido;
        await this.getPedItens(pedido);
        this.ped.itens = this.pedItens || [];
      }
    },
    AddItem(item) {
      if (item.quantidade <= 0) {
        this.showErro = !this.showErro;
        this.showErroText = "A quantidade deve ser maior que zero!";
        return;
      }
      if (item.item == null || item.item == "") {
        this.showErro = !this.showErro;
        this.showErroText = "O item deve ser informado!";
        return;
      }
      console.log(this.ped);
      this.ped.itens.push({
        descricao: item.item,
        quantidade: item.quantidade,
        id: Math.floor(Math.random() * 100000000) + 1,
      });
      item = {
        quantidade: 1,
        item: null,
      };
    },
    delItem(id) {
      console.log(this.ped.itens);
      this.ped.itens = this.ped.itens.filter((value) => value.id !== id);
    },
    ...mapActions("pedido", ["getPedido"]),
    ...mapActions("pedItem", ["getPedItens"]),
    ...mapActions("produto", ["getProdutosList"]),
  },
  mounted() {
    this.getPedido();
    this.getProdutosList();
  },
};
</script>
