<template>
  <TelaBase title="Dashboard">
    <v-container>
      <v-card>
        <v-list v-if="notificacao.length > 0" two-line>
          <v-list-item v-for="nota in notificacao" :key="nota.id">
            <v-list-item-title>{{ nota.titulo }}</v-list-item-title>
            <v-list-item-subtitle>{{ nota.descricao }}</v-list-item-subtitle>
            <v-divider></v-divider>
          </v-list-item>
        </v-list>
        <v-card-text v-else>Sem notificações atualmente</v-card-text>
      </v-card>
    </v-container>
  </TelaBase>
</template>
<script>
import TelaBase from "../components/TelaBase.vue";
import { mapActions, mapGetters } from "vuex";

export default {
  components: {
    TelaBase,
  },
  computed: {
    ...mapGetters("notificacao", ["notificacao"]),
  },
  methods: {
    ...mapActions("notificacao", ["getNotificacao"]),
  },
  mounted() {
    this.getNotificacao();
  },
};
</script>
