import Axios from "axios";

export default class PedItem {
  static getPedItens = async (data) => {
    return await Axios.get(`http://${self.location.hostname}:3000/itens`, {
      params: { pedido: data.id },
      headers: {
        pedido: data.id,
        accessToken: localStorage.getItem("accessToken"),
      },
    });
  };
}
