import Axios from "axios";

export default class Estados {
  static getEstado = async (data) => {
    return await Axios.get(`http://${self.location.hostname}:3000/estado`, {
      params: data,
      headers: { accessToken: localStorage.getItem("accessToken") },
    });
  };
  static getAllEstados = async () => {
    return await Axios.get(`http://${self.location.hostname}:3000/estado`, {
      headers: { accessToken: localStorage.getItem("accessToken") },
    });
  };
}
