import Axios from "axios";

export default class Cidade {
  static getCidade = async (data) => {
    return await Axios.get(`http://${self.location.hostname}:3000/cidade`, {
      params: data,
      headers: { accessToken: localStorage.getItem("accessToken") },
    });
  };
  static getAllCidades = async () => {
    return await Axios.get(`http://${self.location.hostname}:3000/cidade`, {
      headers: { accessToken: localStorage.getItem("accessToken") },
    });
  };
  static getCidadesByEstado = async (data) => {
    return await Axios.get(
      `http://${self.location.hostname}:3000/cidadesByEstado`,
      {
        params: data,
        headers: { accessToken: localStorage.getItem("accessToken") },
      }
    );
  };
}
