import Estado from "@/services/estado";
import * as types from "./mutation-types";

export const getAllEstados = async ({ commit }) => {
  const request = await Estado.getAllEstados();
  commit(types.SET_ESTADO, request.data.Estados);
};

export const getEstado = async ({ commit }, data) => {
  const request = await Estado.getEstado(data);
  commit(types.SET_ESTADO, request.data.Estados);
};
