import Produto from "@/services/produto";
import * as types from "./mutation-types";

export const getProduto = async ({ commit }, data) => {
  const request = await Produto.getProduto(data);
  commit(types.SET_PRODUTO, request.data.Produtos);
};

export const getProdutos = async ({ commit }, data) => {
  const request = await Produto.getProdutos(data);
  commit(types.SET_PRODUTO, request.data.Produtos);
};

export const getProdutosList = async ({ commit }, data) => {
  const request = await Produto.getProdutosList(data);
  commit(types.SET_PRODUTO, request.data.Produtos);
};
