<template>
  <div class="py-4">
    <v-img
      class="mx-auto mb-8"
      max-width="200"
      src="../assets/logo.png"
    ></v-img>

    <v-card
      class="mx-auto pa-10 pb-8"
      elevation="8"
      max-width="448"
      rounded="lg"
    >
      <div class="text-subtitle-1 text-medium-emphasis">E-mail</div>
      <v-text-field
        v-model="email"
        density="compact"
        placeholder="E-mail"
        prepend-inner-icon="mdi-email-outline"
        variant="outlined"
      ></v-text-field>

      <div
        class="text-subtitle-1 text-medium-emphasis d-flex align-center justify-space-between"
      >
        Senha
      </div>
      <v-text-field
        v-model="senha"
        :append-inner-icon="visible ? 'mdi-eye-off' : 'mdi-eye'"
        :type="visible ? 'text' : 'password'"
        density="compact"
        placeholder="Digite a sua senha"
        prepend-inner-icon="mdi-lock-outline"
        variant="outlined"
        @click:append-inner="visible = !visible"
      ></v-text-field>

      <v-card class="mb-8" color="surface-variant" variant="tonal">
        <v-card-text class="text-medium-emphasis text-caption">
          Aviso: após 3 tentativas consecutivas de login com erro, sua conta
          será temporariamente bloqueado. Caso isso aconteça entre em contato
          com nós o quanto antes.
        </v-card-text>
      </v-card>

      <v-btn
        block
        class="mb-4"
        size="large"
        variant="tonal"
        @click="gotoLogin()"
      >
        Entrar
      </v-btn>
      <v-btn
        block
        class="mb-4"
        size="large"
        variant="tonal"
        @click="gotoHome()"
      >
        Sair
      </v-btn>
    </v-card>

    <v-snackbar v-model="showErro" multi-line>
      {{ showErroText }}
      <template v-slot:actions>
        <v-btn variant="text" @click="showErro = !showErro"> Fechar </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  data: () => ({
    visible: false,
    showErro: null,
    showErroText: null,
    senha: null,
    email: null,
  }),
  methods: {
    gotoHome() {
      this.$router.push({ name: "home" });
    },
    async gotoLogin() {
      await this.getLogin({ email: this.email, senha: this.senha });
      if (this.token == "") {
        this.showErro = !this.showErro;
        this.showErroText = "Login inválido! Tente novamente!";
        return;
      }
      localStorage.setItem("accessToken", this.token);
      this.$router.push({ name: "dashboard" });
    },
    ...mapActions("login", ["getLogin"]),
  },
  computed: {
    ...mapGetters("login", ["login", "token"]),
  },
};
</script>
