<template>
  <TelaBase title="Produtos">
    <v-container>
      <div>
        <div>
          <v-table>
            <thead style="background-color: lightgrey">
              <tr>
                <td class="text-left">Descrição</td>
                <td class="text-left">Valor clientes</td>
                <td class="text-left">Valor atacado</td>
                <td class="text-left">Estoque</td>
                <td class="text-left">
                  <v-btn class="ma-2" @click="openDialog()"> Novo </v-btn>
                </td>
              </tr>
            </thead>
            <tbody>
              <tr v-for="produto in produtos" :key="produto.id">
                <td class="text-left">{{ produto.nome }}</td>
                <td class="text-left">
                  {{ parseFloat(produto.valor).toFixed(2) }}
                </td>
                <td class="text-left">
                  {{ parseFloat(produto.valor_atacado).toFixed(2) }}
                </td>
                <td class="text-left">
                  {{ parseFloat(produto.estoque).toFixed(2) }}
                </td>
                <td class="text-left">
                  <v-btn class="ma-2" dark @click="openDialog(produto)">
                    Editar
                  </v-btn>
                </td>
              </tr>
            </tbody>
          </v-table>
        </div>
      </div>

      <v-row justify="center">
        <v-dialog
          v-model="dialog"
          hide-overlay
          transition="dialog-bottom-transition"
          persistent
        >
          <v-card title>
            <v-toolbar flat dark>
              <v-btn icon @click="dialog = !dialog">
                <v-icon> mdi-close </v-icon>
              </v-btn>
              <v-toolbar-title>Edição</v-toolbar-title>
              <v-spacer></v-spacer>
              <v-toolbar-items>
                <v-btn text @click="dialog = !dialog"> Excluir </v-btn>
              </v-toolbar-items>
              <v-toolbar-items>
                <v-btn text @click="dialog = !dialog"> Salvar </v-btn>
              </v-toolbar-items>
            </v-toolbar>

            <div>
              <v-responsive class="mx-auto" max-width="90%">
                <v-row>
                  <v-col cols="12" md="4">
                    <v-text-field
                      v-model="newProd.nome"
                      label="Descrição"
                      :rules="rules"
                      clearable
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" md="2">
                    <v-text-field
                      type="number"
                      prefix="$"
                      v-model="newProd.valor"
                      label="Valor clientes"
                      :rules="rules"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" md="2">
                    <v-text-field
                      type="number"
                      prefix="$"
                      v-model="newProd.valor_atacado"
                      label="Valor atacado"
                      :rules="rules"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12" md="3">
                    <v-checkbox
                      v-model="newProd.ativo"
                      label="Produto ativo"
                    ></v-checkbox>
                  </v-col>
                </v-row>
              </v-responsive>
            </div>
          </v-card>
        </v-dialog>
      </v-row>

      <v-snackbar v-model="showErro" multi-line>
        {{ showErroText }}
        <template v-slot:actions>
          <v-btn variant="text" @click="showErro = !showErro"> Fechar </v-btn>
        </template>
      </v-snackbar>
    </v-container>
  </TelaBase>
</template>

<script>
import TelaBase from "../components/TelaBase.vue";
import { mapActions, mapGetters } from "vuex";

export default {
  components: {
    TelaBase,
  },
  data: () => ({
    dialog: false,
    showErro: false,
    showErroText: null,
    newProd: {
      nome: null,
      valor: 0,
      ativo: true,
      valor_atacado: 0,
    },
    rules: [(value) => !!value || "O campo é obrigatório."],
  }),
  computed: {
    ...mapGetters("produto", ["produtos"]),
  },
  methods: {
    openDialog(produto) {
      this.dialog = !this.dialog;
      produto &&
        (this.newProd = {
          nome: produto.nome,
          valor: produto.valor,
          ativo: produto.ativo,
          valor_atacado: produto.valor_atacado,
        });
    },
    ...mapActions("produto", ["getProdutos"]),
  },
  mounted() {
    this.getProdutos();
  },
};
</script>
