import Cidade from "@/services/cidade";
import * as types from "./mutation-types";

export const getAllCidades = async ({ commit }) => {
  const request = await Cidade.getAllCidades();
  commit(types.SET_CIDADE, request.data.Cidades);
};

export const getCidadesByEstado = async ({ commit }, data) => {
  const request = await Cidade.getCidadesByEstado(data);
  commit(types.SET_CIDADE, request.data.Cidades);
};

export const getCidade = async ({ commit }, data) => {
  const request = await Cidade.getCidade(data);
  commit(types.SET_CIDADE, request.data.Cidades);
};
