import Pedido from "@/services/pedido";
import * as types from "./mutation-types";

export const getAllPedidos = async ({ commit }) => {
  const request = await Pedido.getAllPedidos();
  commit(types.SET_PEDIDO, request.data.Pedidos);
};

export const getPedido = async ({ commit }, data) => {
  const request = await Pedido.getPedido(data);
  commit(types.SET_PEDIDO, request.data.Pedidos);
};
