<template>
  <TelaBase title="Dados da empresa">
    <v-form>
      <v-container>
        <v-row>
          <v-col cols="8" sm="5">
            <v-text-field v-model="empLogada.nome" readonly></v-text-field>
          </v-col>
          <v-col cols="8" sm="5">
            <v-text-field v-model="emp.nome" variant="solo"></v-text-field>
          </v-col>
          <v-col cols="8" sm="5">
            <v-text-field v-model="empLogada.cnpj" readonly></v-text-field>
          </v-col>
          <v-col cols="8" sm="5">
            <v-text-field v-model="emp.cnpj" variant="solo"></v-text-field>
          </v-col>
          <v-col cols="8" sm="5">
            <v-text-field v-model="empLogada.telefone" readonly></v-text-field>
          </v-col>
          <v-col cols="8" sm="5">
            <v-text-field v-model="emp.telefone" variant="solo"></v-text-field>
          </v-col>
          <v-col cols="8" sm="5">
            <v-text-field
              v-model="empLogada.estadonome"
              readonly
            ></v-text-field>
          </v-col>
          <v-col cols="8" sm="5">
            <v-autocomplete
              v-model="estadoSelected"
              v-model:search="searchState"
              :loading="loadingState"
              :items="lstEstado"
              hide-no-data
              hide-details
              label="Estado novo"
              density="comfortable"
              variant="solo"
            ></v-autocomplete>
          </v-col>
          <v-col cols="8" sm="5">
            <v-text-field
              v-model="empLogada.cidadenome"
              readonly
            ></v-text-field>
          </v-col>
          <v-col cols="8" sm="5">
            <v-autocomplete
              v-model="cidadeSelected"
              v-model:search="searchCity"
              :loading="loadingCity"
              :items="lstCidades"
              hide-no-data
              hide-details
              label="Cidade nova"
              density="comfortable"
              variant="solo"
            ></v-autocomplete>
          </v-col>
        </v-row>
      </v-container>
    </v-form>
  </TelaBase>
</template>

<script>
import TelaBase from "../components/TelaBase.vue";
import { mapActions, mapGetters } from "vuex";

export default {
  components: {
    TelaBase,
  },
  data: () => ({
    citySelected: null,
    stateSelected: null,
    showErro: false,
    showErroText: null,
    searchState: null,
    searchCity: null,
    loadingState: false,
    loadingCity: false,
    empLogada: {
      id: null,
      nome: null,
      cpnj: null,
      telefone: null,
      endereco: null,
      cidade: {
        id: null,
        descricao: null,
      },
      estado: {
        sigla: null,
        descricao: null,
      },
    },
    emp: {
      id: null,
      nome: null,
      cpnj: null,
      telefone: null,
      endereco: null,
      cidade: {
        id: null,
        descricao: null,
      },
      estado: {
        sigla: null,
        descricao: null,
      },
    },
    lstEstado: [],
    lstCidades: [],
    rules: [(value) => !!value || "O campo é obrigatório."],
  }),
  watch: {
    empresas(value) {
      value && (this.empLogada = value[0]);
    },
    estadoSelected(value) {
      if (!value || value == undefined || value == "") return;
      let estado = this.estados.find((estado) => {
        if (estado.nome == value) return estado;
      });
      this.getCidadesByEstado({ estado: estado.sigla });
    },
    searchCity(val) {
      val && val !== this.citySelected && this.queryCitySelections(val);
    },
    searchState(val) {
      val && val !== this.stateSelected && this.queryStateSelections(val);
    },
  },
  computed: {
    ...mapGetters("empresa", ["empresas"]),
    ...mapGetters("estado", ["estados"]),
    ...mapGetters("cidade", ["cidades"]),
  },
  methods: {
    queryCitySelections(v) {
      this.lstCidades = [];
      this.loadingCity = true;

      setTimeout(() => {
        const dados = this.cidades.filter((e) => {
          return (
            (e.nome || "").toLowerCase().indexOf((v || "").toLowerCase()) > -1
          );
        });
        dados &&
          dados.forEach(
            (value) =>
              !this.lstCidades.includes(value.nome) &&
              this.lstCidades.push(value.nome)
          );
        this.loadingCity = false;
      }, 500);
    },
    queryStateSelections(v) {
      this.cidadeSelected = null;
      this.lstEstado = [];
      this.loadingState = true;

      setTimeout(() => {
        const dados = this.estados.filter((e) => {
          return (
            (e.nome || "").toLowerCase().indexOf((v || "").toLowerCase()) > -1
          );
        });
        dados &&
          dados.forEach(
            (value) =>
              !this.lstEstado.includes(value.nome) &&
              this.lstEstado.push(value.nome)
          );
        this.loadingState = false;
      }, 500);
    },
    ...mapActions("empresa", ["getEmpresa"]),
    ...mapActions("estado", ["getAllEstados"]),
    ...mapActions("cidade", ["getCidadesByEstado"]),
  },
  mounted() {
    this.getEmpresa("1");
    this.getAllEstados();
  },
};
</script>
