<template>
  <div>
    <v-navigation-drawer app permanent v-model="dialogs">
      <v-sheet
        color="grey lighten-4"
        class="pa-4"
        @click="redireciona('dashboard')"
      >
        <v-icon style="margin: 5px">mdi-domain</v-icon>
        {{ nameEmp }}
      </v-sheet>
      <v-divider></v-divider>
      <div v-for="[icon, text, rota] in links" :key="icon" link>
        <v-sheet class="pa-4 isHovering" @click="redireciona(rota)">
          <v-icon style="margin: 5px"> {{ icon }} </v-icon>
          {{ text }}
        </v-sheet>
      </div>
    </v-navigation-drawer>
    <v-main>
      <v-app-bar density="compact">
        <template v-slot:prepend>
          <v-app-bar-nav-icon @click="dialogs = !dialogs"></v-app-bar-nav-icon>
        </template>
        <v-app-bar-title>{{ title }}</v-app-bar-title>
      </v-app-bar>
      <slot></slot>
    </v-main>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

export default {
  name: "telaBase",
  props: {
    title: String,
  },
  data: () => ({
    dialogs: false,
    links: [
      ["mdi-cart-variant", "Pedidos", "pedidos"],
      ["mdi-call-split", "Separação", "separacao"],
      ["mdi-basket", "Produtos", "produtos"],
      ["mdi-book-edit-outline", "Dados empresa", "empresa"],
      ["mdi-alert-octagon", "Sair", "home"],
    ],
    nameEmp: null,
  }),
  methods: {
    redireciona(rota) {
      this.$router.push({ name: rota });
    },
    ...mapActions("empresa", ["getEmpresa"]),
  },
  watch: {
    empresas(value) {
      value && (this.nameEmp = value[0].nome);
    },
  },
  mounted() {
    this.dialogs = !this.dialogs;
    this.getEmpresa();
  },
  computed: {
    ...mapGetters("empresa", ["empresas"]),
  },
};
</script>
